.kukun-docs-example {
    @import 'patrials/mixins',
    'patrials/colors',
    'patrials/fonts',
    'patrials/containers',
    'patrials/scroll-bars',
    'patrials/buttons',
    'patrials/forms/inputs',
    'patrials/borders-shadows',
    'patrials/cards',
    'patrials/kukun-icon-colors',
    'patrials/cursors',
    'patrials/width-options',
    'patrials/banners',
    'modules/carousel',
    'modules/modal',
    'patrials/grid',
    'patrials/pagination',
    'modules/accordion',
    'modules/toast',
    'patrials/color-utils';
    // 'modules/tabs';

body, html {
font-family: $Main-font;
color: $c-primary-dark;
}


.icon-wrapper {
margin: 0;
display: flex;
flex-flow: row wrap;
justify-content: space-between;
li {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    border: 1px solid #cdcdcd;
    border-radius: 50%;
}
.white-icon {
    background-color: #cdcdcd;
    border: 1px solid #999;
}
}

}

